'use client';

export default function MapPage() {
  const arrayA = [1, 4, 9, 16];
  const arrayB = arrayA.map((v, i) => {
    // return v * 2;
    return <li key={i}>{v * 2}</li>;
  });

  console.log(arrayB);

  return (
    <>
      <h1>JSX 陣列 map 範例</h1>
      <hr />
      <ul>{arrayB}</ul>
    </>
  );
}
